import { useEffect, useRef, useState } from 'react'

export const isFalsy = (val: unknown) => val === 0 ? false : !val
export const isVoid = (value: unknown) =>
  value === undefined || value === null || value === "";


// 去除空数据

// let a: object
// a = {name: 'jack'}
// a = () => {
// }
// a = new RegExp('')
//
// let b: { [key: string]: unknown }
// b = {name: 'Jack'}
// b = () => {}
export const cleanObject = (object: {[key: string]: unknown}) => {
  const res = {...object}
  Object.keys(object).forEach(key => {
    const value = object[key]
    if(isVoid(value)) {
      delete res[key]
    }
  })
  return res
}

// 封装一个用于初始化使用的hook，只执行一次（类似于class组件时期的componentDidMount生命周期）
export const useMount = (callback: () => void) => {
  useEffect(() => {
    // TODO 依赖项里加上callback会造成无限循环，这个和useCallback以及useMemo有关系
    // eslint-disable-next-line react-hooks/exhaustive-deps
    callback()
  }, [])
}

// 注意：hook封装的必要性应该是这个hook中有涉及到状态的变化，而且状态不是直接就可以转换出来的，通常要借助其他的hook，
// 比如useEffect,这个时候就可以封装为hook，如果没有使用其他hook就可以实现功能的话，比如上面的函数不使用hook就可以清除
// 对象中的空数据，这个时候就让它作为一个函数就可以了


// 封装一个debounce
export const useDebounce = <V>(value: V, delay?: number) => {
  const [debounceValue, setDebounceValue] = useState(value)
  useEffect(() => {
  // 每次value变化之后，设置一个定时器
    const timeout = setTimeout(() => setDebounceValue(value), delay)
  // 频繁调用时清除上一次的定时器,重新计时
    return () => clearTimeout(timeout)
  }, [value, delay])

  return debounceValue
}

export const useDocumentTitle = (title: string, keepOnUnmount = true) => {
  const oldTitle = useRef(document.title).current;
  // 页面加载时: 旧title
  // 加载后：新title

  useEffect(() => {
    document.title = title;
  }, [title]);

  useEffect(() => {
    return () => {
      if (!keepOnUnmount) {
       // 如果不指定依赖，读到的就是旧title
        document.title = oldTitle;
      }
    };
  }, [keepOnUnmount, oldTitle]);
};

export const resetRoute = () => (window.location.href = window.location.origin);

/**
 * 传入一个对象，和键集合，返回对应的对象中的键值对
 * @param obj
 * @param keys
 */
export const subset = <
  O extends { [key in string]: unknown },
  K extends keyof O
>(
  obj: O,
  keys: K[]
) => {
  const filteredEntries = Object.entries(obj).filter(([key]) =>
    keys.includes(key as K)
  );
  return Object.fromEntries(filteredEntries) as Pick<O, K>;
};

/**
 * 返回组件的挂载状态，如果还没挂载或者已经卸载，返回false；反之，返回true
 */
export const useMountedRef = () => {
  const mountedRef = useRef(false);

  useEffect(() => {
    mountedRef.current = true;
    return () => {
      mountedRef.current = false;
    };
  });

  return mountedRef;
};